import React, { useEffect, useState } from 'react';
import { Helmet } from 'react-helmet';
import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';
import { useQueryParam, StringParam } from 'use-query-params';
import mock from '../../images/mock.jpg';

const ForyouIndexPage = () => {
  const [uuid] = useQueryParam('uuid', StringParam);
  const [isNotFound, setIsNotFound] = useState(false);
  const [friendName, setFriendName] = useState('');
  const [yourName, setYourName] = useState('');
  const [partnerLastName, setPartnerLastName] = useState('');
  const [partnerFirstName, setPartnerFirstName] = useState('');
  const [partnerId, setPartnerId] = useState('');

  useEffect(() => {
    const f = async () => {
      try {
        const res = await fetch(
          `${process.env.API_URL}/api/partners/clients/friends/${uuid}`
        );

        if (res.status === 404) {
          setIsNotFound(true);
          throw new Error(`Bad response /api/partners/clients/friends/{uuid}`);
        }

        const data = await res.json();

        setFriendName(data.friendName);
        setYourName(data.yourName);
        setPartnerLastName(data.partnersClient.partner.lastNameKanji);
        setPartnerFirstName(data.partnersClient.partner.firstNameKanji);
        setPartnerId(data.partnersClient.partnerId);
      } catch (e) {
        console.log(e);
      }
    };

    f();
  });

  if (isNotFound) {
    return (
      <Typography align='center' mt={5}>
        お探しのページは削除されたか見つかりません。
        <br />
        <a href='https://daruma-inv.com/'>https://daruma-inv.com/</a>
      </Typography>
    );
  }

  return (
    <>
      <Helmet>
        <style>
          {`
        *,html,body{
          margin: 0;
          padding: 0;
        }
        a {
          color: #fff !important;
        }
        `}
        </style>
      </Helmet>
      <Box>
        <Box
          sx={{
            width: '100vw',
            height: '680vw',
            backgroundSize: 'cover',
            backgroundPosition: 'center top',
            backgroundRepeat: 'no-repeat',
            backgroundImage: `url(${mock})`
          }}
        >
          <Typography
            color='#fff'
            fontSize='3vw'
            fontWeight='bold'
            sx={{
              pl: '30vw',
              pt: '15vw'
            }}
          >
            こんにちは
            <br />
            {friendName}様
          </Typography>
          <Typography
            color='#fff'
            fontSize='2vw'
            fontWeight='bold'
            sx={{
              pl: '30vw',
              pt: '2vw'
            }}
          >
            {yourName}様より
            <br />
            コンパクトウシのご案内が
            <br />
            届いております。
          </Typography>
          <Typography
            color='#fff'
            fontSize='1vw'
            sx={{
              pl: '30vw',
              pt: '2vw'
            }}
          >
            （担当者： {partnerLastName} {partnerFirstName}）
            <a
              href={`/partners?uuid=${partnerId}`}
              style={{
                color: '#fff'
              }}
              target='_blank'
              rel='noopener noreferrer'
            >
              自己紹介
            </a>
          </Typography>
        </Box>
      </Box>
    </>
  );
};

export default ForyouIndexPage;
